import React, { Component } from 'react';
import axios from 'axios';

const launchurl="https://api.spaceXdata.com/v3/launches?limit=100&launch_success="


 class Launch extends Component {
     launchfilter = (event) => {
         let trueval  = Number(event.target.value);
         let truev= isNaN(trueval);
         let finallaunchurl=`${launchurl}${truev}`;
         console.log(finallaunchurl)
         axios.get(finallaunchurl)
         .then((response) => {this.props.launchdata(response.data)})
     }

     launchfilterfalse = (event) => {
        let finallandingurl = `${launchurl}false`;
        axios.get(finallandingurl)
        .then((response) => {this.props.launchdata(response.data)})
         
    }
    render() {
        return (
            <div>
                 <table cellspacing="18px" border="0">
                <tr>
                    <td onClick={this.launchfilter} value="true">True</td>
                     <td onClick={this.launchfilterfalse} value="false">False</td> 
                </tr>
            </table>
            </div>
        )
    }
}
export default Launch;