import React, { Component } from 'react';
import '../Home/home.css';

const yearurl="https://api.spaceXdata.com/v3/launches?limit=100&launch_success=true&land_success=true&launch_year=";


class Year extends Component {
  
     yearfilter = (event) => {
         let yearvalue = Number(event.target.value);
         let finalyearurl =`${yearurl}${yearvalue}`;
        console.log(finalyearurl);

             fetch(finalyearurl,{Method:'GET'})
             .then((res) =>  res.json())
             .then((data) => {this.props.yeardata(data)})
        
        
        
     }

    render() {
        return (
  <React.Fragment>
            <div>      
                <table style={{cellSpacing:"24px", border:"0"}}>
                    <tr>
                        <td> <button onClick={this.yearfilter} type="button"  value="2006" style={{backgroundColor:"#d0f0c0", border:"none"}}>2006</button></td>
                         <td> <button onClick={this.yearfilter} type="button"  value="2007" style={{backgroundColor:"#d0f0c0", border:"none"}}>2007</button></td>
                    </tr>
                    <tr>
                           <td> <button onClick={this.yearfilter} type="button"  value="2008" style={{backgroundColor:"#d0f0c0", border:"none"}}>2008</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2009" style={{backgroundColor:"#d0f0c0", border:"none"}}>2009</button></td>
                    </tr>
                    <tr>
                           <td> <button onClick={this.yearfilter} type="button"  value="2010" style={{backgroundColor:"#d0f0c0", border:"none"}}>2010</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2011" style={{backgroundColor:"#d0f0c0", border:"none"}}>2011</button></td>   
                    </tr>
                    <tr>
                    <td> <button onClick={this.yearfilter} type="button"  value="2012" style={{backgroundColor:"#d0f0c0", border:"none"}}>2012</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2013" style={{backgroundColor:"#d0f0c0", border:"none"}}>2013</button></td>
                    </tr>
                    <tr>
                    <td> <button onClick={this.yearfilter} type="button"  value="2014" style={{backgroundColor:"#d0f0c0", border:"none"}}>2014</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2015" style={{backgroundColor:"#d0f0c0", border:"none"}}>2015</button></td> 
                    </tr>
                    <tr>
                    <td> <button onClick={this.yearfilter} type="button"  value="2016" style={{backgroundColor:"#d0f0c0", border:"none"}}>2016</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2017" style={{backgroundColor:"#d0f0c0", border:"none"}}>2017</button></td> 
                    </tr>
                    <tr>
                    <td> <button onClick={this.yearfilter} type="button"  value="2018" style={{backgroundColor:"#d0f0c0", border:"none"}}>2018</button></td>
                            <td> <button onClick={this.yearfilter} type="button"  value="2019" style={{backgroundColor:"#d0f0c0", border:"none"}}>2019</button></td>
                    </tr>
                    <tr>
                    <td> <button onClick={this.yearfilter} type="button"  value="2020" style={{backgroundColor:"#d0f0c0", border:"none"}}>2020</button></td>
                    </tr>
                    
                    </table>
            </div>
  </React.Fragment>
            
        )
    }
}
export default Year;