import React from 'react';
import './home.css';
import ContentLoader from 'react-content-loader';
const DisplayHome= (props) => {
   const renderBox = ({quickdata}) => {
       if(quickdata){
           return quickdata.map((item)=>{
               return(
                <div  id="tile2" style={{backgroundColor:"#dcdcdc"}}>
                <div  id="tilecomponent1">
                    <img className="image" src={item.links.mission_patch}/>
                </div>
                <div id="tilecomponent2">
                    <div id="head">
                        {item.mission_name}
                    </div>
                    <br/>
                    <div id="info" style={{fontSize:"22px"}}>
                        Flight no: {item.flight_number}
                    </div>
                    <br/>
                    <br/>
                    <div id="info">
                       year: {item.launch_year}
                    </div>
                    <div id="info">
                        Launch date:{item.launch_date_unix}
                    </div>
                    <div id="info">
                        Rocket type:{item.rocket.rocket_type}
                    </div>
                    <div></div>
                    <br/>
                </div>
            </div>
               )
           })
       }
       else{
           return(
            <ContentLoader>
            <rect x="0" y="0" rx="5" ry="5" width="70" height="70" />
            <rect x="80" y="17" rx="4" ry="4" width="300" height="13" />
            <rect x="80" y="40" rx="3" ry="3" width="250" height="10" />
          </ContentLoader>   
           )
     }
   }
    return (
         <React.Fragment>
             {renderBox(props)}
         </React.Fragment>    
    )
}
export default DisplayHome;