import React,{Component} from 'react';
import './home.css';
import DisplayHome from './DisplayHome';
import Year from '../Filter/year';
import Launch from '../Filter/launch';
import Landing from '../Filter/landing';

const progurl = "https://api.spaceXdata.com/v3/launches?limit=100"

class Home extends Component{
    constructor(){
        super()

        this.state={
            program:'',
            mission:'',
            lyear:''
           
        }
    }

    setDataAsPerFilter(sortedData){
        this.setState({program:sortedData});
    }

    render(){  console.log(this.state.program)
        return(
           <React.Fragment>
<body style={{backgroundColor:"#dcdcdc"}}>
<div>
        <div id="heading">SpaceX Launch Programs</div>
         <br/>
        <div id="tile1">
            <div style={{fontSize:"30px", fontWeight:"bold"}}>Filters</div>
            <div style={{fontSize:"22px",textAlign:"center"}}>
                Launch Year
            </div>
            <hr style={{marginLeft:"5%",marginRight:"5%"}}/>
            <div style={{marginTop:"1px"}}>
                <Year yeardata={(data) => {this.setDataAsPerFilter(data)}}/>
             </div>  
            <div style={{fontSize:"22px", textAlign:"center"}}>
                Successful Launch
                <hr style={{marginLeft:"5%",marginRight:"5%"}}/>
            </div>
           <Launch launchdata={(data) => {this.setDataAsPerFilter(data)}}/>
            <div style={{fontSize:"22px", textAlign:"center"}}>
                Successful Landing
                <hr style={{marginLeft:"5%",marginRight:"5%"}}/>
            </div>
            <Landing landdata={(data) => {this.setDataAsPerFilter(data)}}/>
        </div>
           <DisplayHome quickdata={this.state.program}/>
     </div> 
     </body>
           </React.Fragment>
         
        )
    }
    componentDidMount(){
        fetch(progurl,{Method:'GET'})
        .then((res) =>  res.json())
        .then((data) => {
            this.setState({program:data});
    })
        
    }
}

   
export default Home;