import React, { Component } from 'react';
import axios from 'axios'

const landingurl="https://api.spaceXdata.com/v3/launches?limit=100&launch_success=true&land_success="



 class Landing extends Component {
     landfilter = (event) => {
    let trueval= Number(event.target.value);
    let truev = isNaN(trueval);
    let finallandingurl =`${landingurl}${truev}`;
    console.log(finallandingurl)
    axios.get(finallandingurl)
    .then((response) => {this.props.landdata(response.data)})
}

landfilterflase = (event) => {
    let finallandingurl = `${landingurl}false`;
    axios.get(finallandingurl)
    .then((response) => {this.props.landdata(response.data)})
     
}
    render() {
        return (
            <div>
             <table cellspacing="18px" border="0">
                <tr>
                    <td onClick={this.landfilter} type="button" value="true">True</td>
                     <td onClick={this.landfilterflase} type="button" value="false"> False</td> 
                </tr>
            </table>
            </div>
        )
    }
}
export default Landing;